import React from 'react';
import BlogContentSectionTitle from './components/BlogContentSectionTitle.js';
import Card from './components/Card.js';

import CardThree from './components/CardThree.js';
import HomeIntro from './components/HomeIntro.js';
import ChiFlat from './pages/images/chi-flat.png';
import KhelgharFlat from './pages/images/khelghar-flat.jpg';
import ShawFlat from './pages/images/shawFlat.png';
import BlogContentShort from './components/BlogContentShort.js';
import Dividerblank from './components/Dividerblank.js';

export default class App extends React.Component {
  render() {
    return (
      <div class="">
        <div class="row justify-content-center">
          <div class="col-xl-6 col-lg-6 col">
            <HomeIntro />
          </div>

        </div>


        <BlogContentShort
                  ContentShortTitle="WORKING AT D. E. SHAW"
                  ContentShort= "At D. E. Shaw, I spearhead UX for multiple finance verticals - equities, bonds, convertible securities and fundamental equities as well as for infrastructure verticals - access management and data visualization and analytics. 
                  "
                  Link= ""
                  LinkText="Read more about it here."
                  />

<BlogContentShort
                  ContentShortTitle="INTERNING AT GOOGLE"
                  ContentShort= "At Google, I worked with the G Suite Team to create enterprise experiences for power users. In the 10 weeks, I worked on research, design and testing for a new feature within the Admin Console. I also carved out time to contribute to user research that the Next Billion Users team was conducting. 
                  "/>

      <Dividerblank/>
        <BlogContentSectionTitle
          ContentSectionTitle1="PROJECTS"
        />
      <Dividerblank/>



        <div class="row justify-content-center">
          <div class="col-xl-6 col-lg-6 col ">
            <div class="blog-margin">

              <Card
                title="Power Up"
                featureImage={ShawFlat}
                tags="Interview task for D.E. Shaw"
                description="How to help traders analyze large amounts of data? How to align new products within their existing workflows?"
                route="Shaw"
                linkText="View project"
                Tags2="Research Project, 3 months"
              />

              <Card
                title="Parental Engagement in Rural India"
                featureImage={ChiFlat}
                tags="CHI 2020, SRC 2nd place"
                description="How do we assist teachers to engage parents in their children's education? Will parents engage?"
                route="ParentalEngagement"
                linkText="View project"
              />
              <Card
                title="Skipped!"
                featureImage={KhelgharFlat}
                tags="Service Design for an NGO"
                description="How do create a service for teachers to help them innovate in their classes?"
                route="shaw"
                linkText="View project"
              />
              <CardThree 
              title1="Game Design"
              tags1="Theory and application to create physical and digital games."
              title2="Teacher Training"
              tags2="Service Design for an education    NGO in Pune"
              title3="Enthu"
              tags3="Interview assignment for Google Interview"/>




            </div>     </div>
        </div>
      </div>



    );
  }
}


